        </div>
        <div class="tabs">
            <span
                class="u-tab"
                :class="{ active: active == item.label }"
                v-for="(item, i) in tabs"
                :key="i"
                @click="change(item.label)"
            >
                {{ item.label }}<i>{{ item.name }}</i>
            </span>
        </div>
    </div>
</template>
<script>
export default {
    name: "Tabs",
    emits: ["update"],
    data: function () {
        return {
            tabs: [
                { name: "", label: "ARTICLES" },
                { name: "", label: "AUTHORS" },
            ],
            active: "SLIDER",
        };
    },
    computed: {
        key() {
            return this.$route.params.key;
        },
    },
    methods: {
        change(name) {
            this.active = name;
        },
    },
    watch: {
        key: {
            immediate: true,
            handler: function () {
                const key = {
                    sci: "ARTICLES",
                    authors: "AUTHORS",
                };
                this.active = key[this.key] || "SLIDER";
            },
        },
        active() {
            this.$emit("update", this.active);
            const key = {
                SLIDER: "index",
                ARTICLES: "sci",
                AUTHORS: "authors",
            };
            this.$router.push({ params: { key: key[this.active] } });
        },
    },
};
</script>
